<template>
  <div class="videoPage">
    <div class="row">
      <div class="col-lg-8 col-sm-12">
        <h1 class="title">{{ video.snippet.title }}</h1>
        <div class="ratio ratio-16x9">
          <iframe
            class="embed-responsive-item"
            :src="videoUrl"
            allow="fullscreen"
          />
        </div>
        <div class="text">
          <h5 class="channelTitle">{{ video.snippet.channelTitle }}</h5>
          <h6 class="publishTime">
            {{ videoPublishTime }}
          </h6>
          <hr />
          <p class="description">{{ video.snippet.description }}</p>
        </div>
      </div>
      <div class="col-lg-4 col-sm-12">
        <videoList :videos="videos" @changepage="changeContentPage" />
      </div>
    </div>
  </div>
</template>

<script>
import videoList from "../videoList/videoList.vue";

export default {
  name: "videoPage",
  components: { videoList },
  props: {
    video: Array,
    videos: Array,
  },
  data() {
    return {
      month: [
        "Jan",
        "Feb",
        "Mar",
        "Apr",
        "May",
        "Jun",
        "Jul",
        "Aug",
        "Sep",
        "Oct",
        "Nov",
        "Dec",
      ],
    };
  },
  computed: {
    videoUrl() {
      const { videoId } = this.video.id;
      return `https://www.youtube.com/embed/${videoId}`;
    },
    videoPublishTime() {
      //Sep, 20, 2021
      return (
        this.month[
          this.video.snippet.publishTime[5] +
            this.video.snippet.publishTime[6] -
            1
        ] +
        ", " +
        (this.video.snippet.publishTime[8] +
          this.video.snippet.publishTime[9]) +
        ", " +
        (this.video.snippet.publishTime[0] +
          this.video.snippet.publishTime[1] +
          this.video.snippet.publishTime[2] +
          this.video.snippet.publishTime[3])
      );
    },
  },
  methods: {
    changeContentPage(page, video) {
      this.video = video;
      this.$emit("changepage", page);
    },
  },
};
</script>

<style>
@import "./videoPage.css";
</style>