        <a v-on:click="showSidebar"
          ><span class="iconify" data-icon="fontisto:nav-icon-a"></span
        ></a>
      </div>
      <hr />
      <div class="div-page-link">
        <a v-on:click="setSlidebarCurrentPage(1)"><h4>Home</h4></a>
      </div>
      <div class="div-page-link">
        <a v-on:click="setSlidebarCurrentPage(2)"><h4>Games</h4></a>
      </div>
      <div class="div-page-link">
        <a v-on:click="setSlidebarCurrentPage(3)"><h4>Sports</h4></a>
