    <div class="vuetube-navbar">
      <div class="content">
        <div class="vuetube-icons">
          <div class="dropdown">
            <a v-on:click="setShowSlidebar"
              ><span class="iconify" data-icon="fontisto:nav-icon-a"></span
            ></a>
          </div>
          <div class="navbar-brand">
            <img class="vue-logo" :src="require('../../assets/vue-logo.png')" />
          </div>
        </div>

        <div class="vuetube-navbar-links">
          <a v-on:click="setCurrentPage(1)"
            ><span class="iconify icon icon-home" data-icon="carbon:home"></span
            >Home</a
          >
          <a v-on:click="setCurrentPage(2)"
            ><span
              class="iconify icon icon-controller"
              data-icon="dashicons:games"
            ></span
            >Games</a
          >
          <a v-on:click="setCurrentPage(3)"
            ><span
              class="iconify icon icon-ball"
              data-icon="ic:outline-sports-soccer"
            ></span
            >Sports</a
          >
          <a v-on:click="setCurrentPage(4)"
            ><span
              class="iconify icon icon-music"
              data-icon="eva:music-outline"
            ></span
            >Music</a
          >
