        <div class="vuetube-search">
          <div class="vuetube-input-search">
            <input
              type="text"
              @input="setInputValue"
              v-on:keyup.enter="updateSearchInput"
            />
            <a class="icon-search" v-on:click="updateSearchInput"
              ><span
                class="iconify"
                data-icon="ant-design:search-outlined"
                data-width="16"
              ></span
