<script>
export default {
  name: "navbar",
  data() {
    return { currentPage: 1, showSlidebar: false, inputSearchValue: "" };
  },
  methods: {
    checkCurrentPage(page) {
      return this.currentPage === page;
    },
    setStyle() {
      return "background-color: 'black'";
    },
    setInputValue(event) {
      this.inputSearchValue = event.target.value;
    },
    setShowSlidebar() {
      this.showSlidebar = !this.showSlidebar;
    },
    setSlidebarCurrentPage(page) {
      this.setCurrentPage(page);
      this.setShowSlidebar();
    },
    setCurrentPage(page) {
      this.currentPage = page;
      this.updateValues(false, page);
    },
    updateSearchInput() {
      this.updateValues(this.inputSearchValue, false);
    },
  },
  props: ["updateValues"],
};
</script>

