<template>
  <div class="mainContent">
    <videoList
      @changepage="changeContentPage"
      v-if="checkContentPage('videoList')"
      :videos="videos"
    />
    <videoPage
      v-if="checkContentPage('videoPage')"
      :video="video"
      :videos="videos"
    />
  </div>
</template>

<script>
import videoList from "./videoList/videoList.vue";
import videoPage from "./videoPage/videoPage.vue";

export default {
  name: "mainContent",
  props: {
    videos: Array,
    contentPage: String,
  },
  data() {
    return { video: "" };
  },
  components: {
    videoList,
    videoPage,
  },
  methods: {
    checkContentPage(page) {
      return this.contentPage === page;
    },
    changeContentPage(page, video) {
      this.video = video;
      this.$emit("changepage", page);
    },
  },
};
</script>

<style>
@import "./mainContent.css";
</style>