<template>
  <div id="app">
    <navbar searchInput="searchInput" :updateValues="updateValues" />
    <mainContent
      @changepage="changeContentPage"
      :videos="videos"
      :contentPage="contentPage"
    />
    <footerComponent />
  </div>
</template>

<script>
import axios from "axios";
import navbar from "./components/navbar/navbar.vue";
import mainContent from "./components/mainContent/mainContent.vue";
import footerComponent from "./components/footerComponent/footerComponent.vue";
const API_KEY = "AIzaSyDLj0AaR0OCJ9krtWoHzY4mxFQAw06IyC8";

export default {
  name: "App",
  components: {
    navbar,
    mainContent,
    footerComponent,
  },
  data() {
    return {
      currentPage: 1,
      contentPage: "videoList",
      inputSearchValue: "",
      pageSubject: ["News", "Games", "Sports", "Music"],
      selectedVideo: null,
      videos: [],
    };
  },
  beforeMount() {
    this.onTermChange("News");
  },
  methods: {
    onTermChange(searchTerm) {
      axios
        .get("https://www.googleapis.com/youtube/v3/search", {
          params: {
            key: API_KEY,
            type: "video",
            part: "snippet",
            q: searchTerm,
          },
        })
        .then((response) => {
          this.videos = response.data.items;
          //video[0].snippet.title
          //video[0].snippet.description
          //VideoUrl = https://www.youtube.com/embed/video[0].id
          console.log(this.videos);
        });
    },
    setInputSearch(event) {
      this.inputSearch = event.target.value;
    },
    updateValues(newInputSearchValue, newCurrentPage) {
      this.changeContentPage("videoList", "");
      if (newInputSearchValue) {
        this.inputSearchValue = newInputSearchValue;
        this.onTermChange(this.inputSearchValue);
      }
      if (newCurrentPage) {
        this.currentPage = newCurrentPage;
        this.onTermChange(this.pageSubject[newCurrentPage - 1]);
      }
    },
    changeContentPage(page) {
      this.contentPage = page;
    },
  },
};
</script>

