<template>
  <div class="videoList">
    <div v-for="video in videos" :key="video.etag">
      <div class="content col-lg-4 col-md-6 col-sm-12">
        <a v-on:click="setContentPage('videoPage', video)"
          ><h3 class="title">{{ video.snippet.title }}</h3></a
        >
        <div class="imageContent">
          <a v-on:click="setContentPage('videoPage', video)"
            ><img :src="video.snippet.thumbnails.default.url"
          /></a>
          <div>
            <p class="description">{{ video.snippet.description }}</p>
            <a class="link" v-on:click="setContentPage('videoPage', video)"
              >Watch now<span
                class="iconify video-icon"
                data-icon="el:video"
              ></span
            ></a>
          </div>
        </div>
        <hr />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "VideoList",
  props: {
    videos: Array,
  },
  methods: {
    setContentPage(page, video) {
      this.$emit("changepage", page, video);
    },
  },
};
</script>

